.sentence {
  margin: 0;
  padding: 0;
  font-size: 5px;
  font-weight: 300;
  font-family: 'Libre Baskerville', serif;
  font-style: bold;
  color: rgb(206, 206, 206);
  letter-spacing: 0.4px;
  cursor: default;
}

.container {
  width: 140%;
  height: 115px;
  position: fixed;
  left: 0;
  top: 0;
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  margin: 0;
  z-index: 2000;
}

@media only screen and (max-width: 600px) {
  .container {
    width: 400%;
  }
}

@media screen and (device-aspect-ratio: 40/71) {
  .container {
    width: 460%;
  }
}
